import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import './userTable.css'

function UserTable({user, status, deleteItem}) {

function btnStatusDone(e) {
    console.log('clicked done')
    status(parseInt(e.target.name), 'done')
}
function btnStatusTodo(e) {
    console.log('clicked todo')
    status(parseInt(e.target.name), 'todo')
}
function btnStatusDelete(e) {
    console.log('clicked delete')
    deleteItem(parseInt(e.target.name))
}

  return (
    <Table >
      <thead>
        <tr>
          <th>#</th>
          <th>Title</th>
          <th>Description</th>
          <th>Date</th>
          <th>Update</th>
          <th>Status</th>
        </tr>
      </thead>
      <tbody>
        {user.length > 0
        ? (user.map((item, index) => {
            return (
                <tr key={index} className={item.status}>
                    <td>{index + 1}</td>
                    <td>{item.title}</td>
                    <td>{item.description}</td>
                    <td>{item.dueDay} : {item.dueDate}</td>
                    <td>
                        {item.status === "todo" &&
                        <>
                        <Button name={item.id} variant="outline-primary" size="sm">Edit</Button>{' '}
                        <Button name={item.id} onClick={btnStatusDelete} variant="outline-secondary" size="sm">Delete</Button>
                        </>
                        }
                    </td>
                    <td>
                        {item.status === "done" &&
                        <>
                            <span>DONE </span> 
                            <Button name={item.id} onClick={btnStatusTodo} variant="dark" size="sm">Undo</Button>
                        </>
                        }
                        {item.status === "todo" &&
                        <>
                            <span>Todo </span> 
                            <Button name={item.id} onClick={btnStatusDone} variant="outline-success" size="sm">Mark as Done</Button>
                        </>
                        }
                    </td>
                </tr>
            )
        }))
        : 
        <tr><td colSpan={6}>no data found</td></tr>
        }
      </tbody>
    </Table>
  );
}

export default UserTable;