import React, { useState, forwardRef } from "react";
import DatePicker from "react-datepicker";
import moment from 'moment';
import "react-datepicker/dist/react-datepicker.css";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button, Form, InputGroup, Row } from 'react-bootstrap';
import UserTable from './UserTable'


export default function FormComponent() {

    const [user, setUser] = useState([])
    const [title, setTitle] = useState("")
    const [des, setDes] = useState("")
    const [startDate, setStartDate] = useState(new Date());
    
    function submitHandle(e) {
        e.preventDefault()
        console.log("submit clicked")
        const formatedDate = moment(startDate).format("MM/DD/YYYY")
        const formatedDay = moment(startDate).format('ddd')
        if ((title && des) !== "") {
            setUser((previous) => {
                return [...previous, {
                    id: new Date().valueOf(),
                    title : title, 
                    description: des, 
                    dueDate: formatedDate, 
                    dueDay: formatedDay,
                    status: "todo"
                }]
            })
        }
        setTitle("")
        setDes("")
    }

    function statusHandle(id, todoState) {
        console.log("status", id, todoState)
        const _user = user.map(
            el => el.id === id? { ...el, status: todoState }: el
        )
        setUser(_user)
    }
    function deleteHandle(id) {
        const _user = user.filter((item) => {
            return item.id !== id
        })
        setUser(_user)
    }

    const DateButton = () => {
        const ExampleCustomInput = forwardRef(({ value, onClick }, ref) => (
            <Button variant="link" onClick={onClick} ref={ref} >{value}</Button>
        ));
        const handleCalendarClose = () => {
        //   document.getElementById(moment(startDate).format("MM/DD/YYYY"))?.scrollIntoView({behavior: 'smooth'})
        };
        
        return (
            <DatePicker
            selected={startDate}
            onChange={(date) => setStartDate(date)}
            customInput={<ExampleCustomInput />}
            onCalendarClose={handleCalendarClose}
            variant="link"
            />
        );
    }

    console.log(user)
    return (
        <>
            <div className="h5">TODO Form:</div>
            <Form>
                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                    <Form.Label>Title *</Form.Label>
                    <Form.Control type="text" placeholder="" onChange={(e) => setTitle(e.target.value)} value={title} />
                </Form.Group>
                <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                    <Form.Label>Description *</Form.Label>
                    <Form.Control as="textarea" rows={3} onChange={(e) => setDes(e.target.value)} value={des} />
                </Form.Group>
                <Form.Group className="mb-3" controlId="exampleForm.ControlDateBtn">
                    <Form.Label>Due Date</Form.Label>
                    <DateButton />
                </Form.Group>
                <Form.Group className="mb-3" controlId="exampleForm.ControlSubmitBtn">
                    <Button onClick={submitHandle} variant="primary" size="lg" active>Submit</Button>
                </Form.Group>
            </Form>

            <UserTable user={user} status={statusHandle} deleteItem={deleteHandle}/>

        </>
    )
}